@import url('https://fonts.googleapis.com/css?family=Poppins');

$font-family: 'Poppins', sans-serif;

@mixin flexColumn {
  display: flex;
  flex-direction: column;
}


@mixin font64 {
  font-family: $font-family;
  font-size: 64px;
  font-weight: 800;
  line-height: 79px;
  letter-spacing: -0.035em;
  text-align: left;
}@mixin font60 {
  font-family: $font-family;
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 90px;
}
@mixin  font25 {
  font-family: $font-family;
  font-size: 25px;
  font-weight: 700;
  line-height: 31px;
  letter-spacing: -0.02em;
  text-align: center;
}

@mixin font15 {
  font-family: $font-family;
  font-size: 15px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: left;

}
@mixin font17 {
  font-family: $font-family;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 124%;
  letter-spacing: -0.02em;
}

@mixin font16 {
  font-family: $font-family;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  letter-spacing: 0em;
  text-align: left;
}
@mixin font13 {
  font-family: $font-family;
  font-size: 13px;
  font-weight: 600;
  line-height: 16px;
  letter-spacing: 0em;
  text-align: left;
}
@mixin font20 {
  font-family: $font-family;
  font-size: 20px;
  font-weight: 600;
  line-height: 31px;
  letter-spacing: -0.02em;
  text-align: left;

}
@mixin font30 {
  font-family: $font-family;
  font-size: 30px;
  font-weight: 500;
  line-height: 47px;
  letter-spacing: -0.02em;
  text-align: left;
}
@mixin font22 {
  font-family: $font-family;
  font-size: 22px;
  font-weight: 400;
  line-height: 34px;
  letter-spacing: -0.02em;
  text-align: center;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0px 4px 20px 0px #00000059;
  border-radius: 1000px;

}

::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.2);
  border-radius: 1000px;
}
