
@import "variable";
html, body {
  height: 100vh;
  overflow-x: hidden;
  width: 100vw;
  background:#06061F;

}

#root {
  background:#06061F;
  color: white;
  height: 100vh;
  width: 100vw;
  button {
    outline: none;
    cursor: pointer;
    border: none;
  }
  img {
    max-width: 100%;
    cursor: pointer;
  }
  a{
    text-decoration: none;
    cursor: pointer;
  }
  .home{
    background:url("https://media.discordapp.net/attachments/946293505226067998/964852770438021147/unknown.png?width=792&height=559") no-repeat top left;
    background-size:43%;
    height: 151vh;
    width: 100%;
    .header{
      gap: 99px;
      padding-top: 184px;
      padding-bottom: 120px;
      a{
        text-decoration: none;
      }
      span{
        color: white;
        @include font20();
        &:hover{
          color: #0E7DFF ;
        }
      }
    }
    .Welcome{
      flex-direction: column;
      padding-left: 33%;
      .user{
        color: #0E7DFF!important;
      }
      .welcomeTitle{
        @include font60();
        color: white;
      }
      .title{
        @include font30();
        color: white;
      }
    }
    .buttons{
      gap: 20px;
      padding-left: 33%;
      padding-top: 30px;

      a{
        button{
          background: #FFFFFF33;
          border-radius: 22px;
          color: white;
          padding: 8px 32px;
          border: 1px solid;
          @include font22();
          border-image-source: linear-gradient(110.56deg, rgba(119, 67, 169, 0.6) -0.18%, #000000 63.64%);
        }

      }

    }
  }
  .home_header{
    background: url("https://media.discordapp.net/attachments/946293505226067998/964853602537582622/unknown.png?width=326&height=559") no-repeat bottom right;
    background-size: 41%;
    height: 100%;
    background-position-y: 261px;
  }
  .service{
    background: url("../assets/img/Ellipse.png") no-repeat center center #06061F;
    height: 100vh;
    width: 100%;
    .Title{
      @include font64();
      color: white;
      text-align: center;
      padding-top: 101px;
    }
    .blocks{
      gap: 20px;
      padding: 27px 10% 22px 10%;
      cursor: pointer;
      .default{
        background: #02031F;
        padding: 38px 38px 44px 47px;
        width: 492px;
        height: 265px;
        &:hover{
          transform: scale(1.042);
        }
        .block__title{
          @include font25();
          margin-bottom: 26px;
        }
        .default_title{
          color: white;
          @include font17();
        }
        .clickHere{
          color: #0E7DFF!important;
        }
        .current{
          font-weight: 700!important;
        }
      }
    }
  }
  .contactBottom{
    @include flexColumn();
    gap: 40px;
    .copyright{
      @include font16();
      color: white;
      text-align: center;
    }
  }
  .contacts{
    background-size: 100% 100%!important;
    width: 100%;
    height: 110vh;
    background: url("https://media.discordapp.net/attachments/946293505226067998/964922214023594034/unknown.png?width=620&height=676") no-repeat top center;
    padding-top: 6%;
    .conBack{
      //height: 100vh;
      //background: url("../assets/img/STARS AND RINGS.png") no-repeat bottom right, url("../assets/img/Group 1202.png") no-repeat bottom left ;
      //background-size: 33%;
    }
    .Title{
      color: white;
      @include font60();
      text-align: center;
    }
    .Card{
      border: 1.5px solid;
      border-image-source: linear-gradient(116.5deg, #FFFFFF 6.1%, #000000 95.45%);
      border-radius: 40px;
      padding: 77px 62px 45px 33px;
      margin: 61px 25%;
      gap: 74px;
      //background: url("../assets/img/El.png") no-repeat center center #16224E4F;
      background: #16224E4F;
      .title{
        @include font25();
      }
      .content{
        margin-top: 28px;
        input{
          background: #FFFFFF4D;
          box-shadow: 0px 10px 20px 0px #060E32;
          padding: 10px 20px;
          border: none;
          border-radius: 10px;
          @include font13();
          color: white;
          width: 340px;
          &:focus{
            border: none;
            outline: none;
          }
          &::placeholder{
            color: white;
          }
        }
      }
      .getStarted{
        .content{
          @include flexColumn();
          gap: 15px;
          textarea{
            background: #FFFFFF4D;
            border-radius: 10px;
            resize: none;
            border: none;
            @include font13();
            color: white;
            width: 340px;
            height: 200px;
            padding: 10px 20px;
            outline: none;
            &::placeholder{
              color: white;
            }
          }
        }
      }
      .subtitle{
       @include font16();
      }
    }

    .footer{
      gap: 26px;
    }
  }
  .sentBtn{
    gap: 10px;
    box-shadow: 0px 10px 20px 0px #060E32;
    background: linear-gradient(105.61deg, rgba(60, 16, 185, 0.45) 31.65%, rgba(0, 133, 255, 0.45) 91.12%),
    linear-gradient(0deg, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0.3));
    border-radius: 10px;
    flex-grow: 0;
    padding: 10px 20px;
    margin: 0 auto;
    span{
      @include font13();
      color: white;
    }
  }

  @media screen and (max-width: 1440px) {
    .Welcome,.buttons{
      padding-left: 27%!important;
    }
    .service{
      height: 127vh!important;
    }
    .Card{
      margin: 61px 17% !important;
    }
    .contacts{
      height: 132vh!important;
    }

  }
  @media only screen and (max-width: 1366px) {
    .Card{
      margin: 61px 15% !important;
    }
  }
  @media only screen and (max-width: 1280px) {
    .Card{
      margin: 61px 12% !important;
    }
  }

}
